import React, { useEffect, useState, useRef } from 'react';
import { JSONTree } from 'react-json-tree';
import moment from 'moment';
import './App.css';

function App() {
  const [logFiles, setLogFiles] = useState([]);
  const [selectedFile, setSelectedFile] = useState('');
  const [logs, setLogs] = useState([]);
  const [searchTerm, setSearchTerm] = useState('');
  const [autoScroll, setAutoScroll] = useState(true);
  const [streaming, setStreaming] = useState(false);
  const eventSourceRef = useRef(null);
  const logContainerRef = useRef(null);

  useEffect(() => {
    fetch('/api/files')
      .then(response => response.json())
      .then(data => setLogFiles(data.files))
      .catch(error => console.error('Error fetching files:', error));
  }, []);

  useEffect(() => {
    if (selectedFile) {
      fetch(`/api/download/${selectedFile}`)
        .then(response => response.text())
        .then(data => {
          const logLines = data.split('\n');
          setLogs(logLines);
        })
        .catch(error => console.error('Error loading file:', error));
    }
  }, [selectedFile]);

  const toggleStreaming = () => {
    if (!selectedFile) return;

    if (streaming) {
      console.log(`Stopping streaming for file: ${selectedFile}`);
      eventSourceRef.current.close();
      setStreaming(false);
    } else {
      console.log(`Starting streaming for file: ${selectedFile}`);

      const eventSource = new EventSource(`/api/logs/${selectedFile}`);
      eventSourceRef.current = eventSource;
      setStreaming(true);

      eventSource.onmessage = function(event) {
        setLogs(logs => [...logs, event.data]);
      };

      eventSource.onerror = function(event) {
        console.error('EventSource failed:', event);
        eventSource.close();
        setStreaming(false);
      };
    }
  };

  useEffect(() => {
    if (autoScroll) {
      logContainerRef.current?.scrollTo(0, logContainerRef.current.scrollHeight);
    }
  }, [logs, autoScroll]);

  const filteredLogs = logs.filter(log => log.toLowerCase().includes(searchTerm.toLowerCase()));

  const theme = {
    base00: '#1e1e1e',
    base01: '#282c34',
    base02: '#383c4a',
    base03: '#4b5263',
    base04: '#6272a4',
    base05: '#e9efff',
    base06: '#b45bcf',
    base07: '#f1fa8c',
    base08: '#ff79c6',
    base09: '#ffb86c',
    base0A: '#bd93f9',
    base0B: '#50fa7b',
    base0C: '#8be9fd',
    base0D: '#6272a4',
    base0E: '#ff79c6',
    base0F: '#f8f8f2'
  };

  const formatTimestamp = (timestamp) => {
    return moment(timestamp).format('YYYY-MM-DD HH:mm:ss');
  };

  const renderLog = (log) => {
    try {
      const jsonLog = JSON.parse(log);
      if (jsonLog.time) {
        jsonLog.time = formatTimestamp(jsonLog.time);
      }
      return <JSONTree data={jsonLog} theme={theme} invertTheme={false} hideRoot />;
    } catch (e) {
      return <div className="plain-log">{log}</div>;
    }
  };

  return (
    <div className="App">
      <header className="App-header">
        <div className="controls">
          <select onChange={(e) => setSelectedFile(e.target.value)} value={selectedFile}>
            <option value="">Select a log file</option>
            {logFiles.map(file => (
              <option key={file} value={file}>{file}</option>
            ))}
          </select>
          <button onClick={toggleStreaming} disabled={!selectedFile}>
            {streaming ? 'Pause Streaming' : 'Start Streaming'}
          </button>
          <input
            type="text"
            placeholder="Search logs"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
          />
          <button onClick={() => setAutoScroll(!autoScroll)}>
            {autoScroll ? 'Disable Auto-scroll' : 'Enable Auto-scroll'}
          </button>
          <button onClick={() => window.location.href = `/api/download/${selectedFile}`} disabled={!selectedFile}>
            Download Logs
          </button>
        </div>
        <div id="log-container" className="log-container" ref={logContainerRef}>
          {filteredLogs.map((log, index) => (
            <div key={index} className="log-entry">
              {renderLog(log)}
            </div>
          ))}
        </div>
      </header>
    </div>
  );
}

export default App;
